@tailwind base;
@tailwind components;
@tailwind utilities;


*{
  box-sizing: border-box;
  margin: 0;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}

body{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: darkgray;
  color: white;
}

header{
  padding: 2rem 1rem;
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
}

header h1{
  font-size: 2.5rem;
  font-weight: 300;
  color: black;
  margin-bottom: 1rem;
}

#task_form{
  display: flex;
}

input, button{
  appearance: none;
  border: none;
  outline: none;
  background: none;
} 

#task_Input{
  flex: 1 1 0%;
  background-color: black;
  color: white;
  padding: 1rem;
  border-radius: 1rem;
  margin-right: 1rem;
  font-size: 1rem;
}

