  color: tomato;
  font-size: 1rem;
  font-weight: 600;
  background-image: linear-gradient(to right,blue,red);
  cursor: pointer;
  border-radius: 1rem;

} */

#submit:hover{
  opacity: 0.8;
}

#submit:active{
  opacity: 0.6;
}

main{
  flex: 1 1 0%;
  padding: 2rem 1rem;
  max-width: 800px;
  width: 100%;
  margin: 0 auto;
}

.task{
  padding: 1rem;
}

h2{
  font-size: 1rem;
  font-weight: 300;
  color: yellow;
  margin-bottom: 1rem;
}
#task .task {
  display: flex;
  justify-content: space-between;
  background-color: black;
  padding: 1rem;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
#task .task .content .text{
  color: gray;
  font-size: 1.125rem;
  width: 100%;
  display: block;
  transition: 0.4s;

}
#task .task .content .text:read-only{
  color: white;
}

#task .task .click {
  display: flex;
  margin: 0 -0.5rem;
  color: darkgray;
}

.task .click button{
  cursor: pointer;
  margin: 0 0.5rem;
  font-size: 1.125rem;
  color: gray;
  font-weight: 700;
  text-transform: uppercase;
  transition: 0.4s;


}

.task .click button:hover{
  opacity: 0.8;
}

.task .click button:active{
  opacity: 0.6;
}

.task .click .edit {
  color: yellowgreen;
  background-image: linear-gradient(to right,blue,red);
  border-radius: 1rem;
}

.task .click .delete {
  color: red;
  border-radius: 1rem;
}
